<template>
  <div class="navigation" :class="{ 'dark-theme': currentTheme === 'dark' }">
    
    <div class="search-container">
      <canvas 
        v-if="currentTheme === 'dark'" 
        ref="bannerCanvas" 
        class="banner-matrix-bg"
      ></canvas>

      <div class="search-bar">
        <el-input
          v-model="localSearchQuery"
          placeholder=">"
          clearable
          class="search-input"
          @keydown.enter="performSearch"
        >
          <template #prepend>
            <el-select 
              v-model="searchMode" 
              class="search-mode" 
              style="width: 100px"
              :popper-class="currentTheme === 'dark' ? 'hacker-dropdown' : ''"
            >
              <el-option label="Google" value="Google"></el-option>
              <el-option label="Bing" value="Bing"></el-option>
              <el-option label="Sogou" value="Sogou"></el-option>
              <el-option label="Baidu" value="Baidu"></el-option>
            </el-select>
          </template>
        </el-input>

        <el-button class="search-button" type="default" @click="performSearch">>_ execute</el-button>
      </div>
    </div>

    <div class="pagination-container pagination-top">
      <el-pagination
        v-model:current-page="currentPage"
        :page-size="pageSize"
        :total="filteredCategories.length"
        layout="prev, pager, next"
        @current-change="handlePageChange"
        background
      />
    </div>

    <div v-for="category in paginatedCategories" :key="category.name" class="category" :data-category="category.name">
      <h2 class="category-title">
        <span style="display: inline-flex; margin-right: 4px;">{{ category.emoji }}</span>
        {{ category.name }}
        <span class="category-count">{{ getCategoryCount(category) }}</span>
      </h2>

      <div class="links-container">
        <template v-if="category.subCategories && category.subCategories.length > 0">
          <div v-for="sub in category.subCategories" :key="sub.name" class="sub-category-group">
            <h3 class="subcategory-title">
              <span class="decoration">#</span> {{ sub.name }}
            </h3>
            <ul>
              <li v-for="link in sub.links" :key="link.name">
                <el-tooltip class="item" effect="dark" :content="link.description" placement="top">
                  <a :href="link.url" target="_blank" rel="noopener noreferrer"> 
                    <el-button 
                      :type="currentTheme === 'dark' ? 'primary' : ''" 
                      :plain="currentTheme !== 'dark'"
                      class="link-button"
                    >
                      <img 
                        v-if="getFaviconUrl(link.url)" 
                        :src="getFaviconUrl(link.url)" 
                        :alt="link.name" 
                        class="favicon" 
                        @error="handleImageError($event, link.url)" 
                      />
                      <span class="link-text">{{ link.name }}</span>
                      <template v-if="link.tag || (link.tags && link.tags.length)">
                        <el-tag
                          v-for="(t, idx) in normalizeTags(link)"
                          :key="idx + '-' + t"
                          size="small"
                          :type="getTagType(t)"
                          class="link-mini-tag"
                          effect="plain"
                        >
                          {{ t }}
                        </el-tag>
                      </template>
                    </el-button>
                  </a>
                </el-tooltip>
              </li>
            </ul>
          </div>
        </template>

        <ul v-else>
          <li v-for="link in category.links" :key="link.name">
            <el-tooltip class="item" effect="dark" :content="link.description" placement="top">
              <a :href="link.url" target="_blank" rel="noopener noreferrer"> 
                <el-button 
                    :type="currentTheme === 'dark' ? 'primary' : ''" 
                    :plain="currentTheme !== 'dark'"
                    class="link-button"
                  >
                  <img 
                    v-if="getFaviconUrl(link.url)" 
                    :src="getFaviconUrl(link.url)"
                    loading="lazy"
                    :alt="link.name" 
                    class="favicon" 
                    @error="handleImageError($event, link.url)" 
                  />
                  <span class="link-text">{{ link.name }}</span>
                  <template v-if="link.tag || (link.tags && link.tags.length)">
                    <el-tag
                      v-for="(t, idx) in normalizeTags(link)"
                      :key="idx + '-' + t"
                      size="small"
                      :type="getTagType(t)"
                      class="link-mini-tag"
                      effect="plain"
                    >
                      {{ t }}
                    </el-tag>
                  </template>
                </el-button>
              </a>
            </el-tooltip>
          </li>
        </ul>
      </div>
    </div>

    <div class="pagination-container">
      <el-pagination
        v-model:current-page="currentPage"
        :page-size="pageSize"
        :total="filteredCategories.length"
        layout="prev, pager, next"
        @current-change="handlePageChange"
        background
      />
    </div>
  </div>
</template>

<script>
export default {
  name: 'NavigationLinks',
  props: {
    categories: { type: Array, required: true },
    currentTheme: { type: String, required: true },
    targetCategory: { type: String, default: '' },
  },
  data() {
    return {
      currentPage: 1,
      pageSize: 15,
      localSearchQuery: '',
      searchMode: 'Google',
      bannerInterval: null,
      resizeTimer: null,

      //   2 
      typingText: '', // 
      typewriterPhrases: [
        '...',
        'Initialize search protocol...',
        'System ready. Waiting for input...',
        'Wake up, Neo...',
        'Searching the Matrix...'
      ],
      phraseIndex: 0, // 
      charIndex: 0,   // 
      isDeleting: false, // 
      typeSpeed: 100,    //  (ms)
      deleteSpeed: 50,   //  (ms)
      pauseTime: 2000,   //  (ms)
      typewriterTimer: null // 


    };
  },
  mounted() {
    window.addEventListener('resize', this.handleResize);

    if (this.currentTheme === 'dark') {
      this.$nextTick(() => this.initBannerMatrix());
    }
    //   3 
    this.startTypewriter();

  },
  beforeUnmount() {
    window.removeEventListener('resize', this.handleResize);
    this.stopBannerMatrix();
    //   4 
    if (this.typewriterTimer) clearTimeout(this.typewriterTimer);
  },
  computed: {
    filteredCategories() {
      const query = this.localSearchQuery.trim().toLowerCase();
      if (!query) return this.categories;

      const isUrl = query.startsWith('http') || query.startsWith('www.') || query.includes('.');
      
      const isLinkMatch = (link) => {
        if (isUrl) return link.url.toLowerCase().includes(query);
        return (
          link.name.toLowerCase().includes(query) ||
          (link.description && link.description.toLowerCase().includes(query)) ||
          link.url.toLowerCase().includes(query)
        );
      };

      return this.categories.map(category => {
        if (category.subCategories && category.subCategories.length > 0) {
          const filteredSubs = category.subCategories.map(sub => {
            const links = sub.links || [];
            const filteredLinks = links.filter(isLinkMatch);
            return filteredLinks.length > 0 ? { ...sub, links: filteredLinks } : null;
          }).filter(Boolean);

          if (filteredSubs.length > 0) {
            return { ...category, subCategories: filteredSubs };
          }
        }

        if (category.links && category.links.length > 0) {
          const filteredLinks = category.links.filter(isLinkMatch);
          if (filteredLinks.length > 0) {
            return { ...category, links: filteredLinks };
          }
        }
        return null;
      }).filter(c => c);
    },
    paginatedCategories() {
      const start = (this.currentPage - 1) * this.pageSize;
      return this.filteredCategories.slice(start, start + this.pageSize);
    }
  },
  watch: {
    localSearchQuery() { this.currentPage = 1; },
    targetCategory: {
      handler(newVal) { this.scrollToCategory(newVal); },
      immediate: true
    },
    currentTheme(newVal) {
      if (newVal === 'dark') {
        this.$nextTick(() => this.initBannerMatrix());
      } else {
        this.stopBannerMatrix();
      }
    }
  },
  methods: {
    //   5 
    startTypewriter() {
          const currentPhrase = this.typewriterPhrases[this.phraseIndex];
          
          // 1. 
          if (this.isDeleting) {
            this.typingText = currentPhrase.substring(0, this.charIndex - 1);
            this.charIndex--;
          } 
          // 2. 
          else {
            this.typingText = currentPhrase.substring(0, this.charIndex + 1);
            this.charIndex++;
          }

          //  "_"
          //  placeholder  UI 
          //  ` + '_'` 
          if (this.currentTheme === 'dark') {
            // 
            // this.typingText += '_'; 
          }

          // 3. 
          let nextSpeed = this.typeSpeed;
          if (this.isDeleting) nextSpeed = this.deleteSpeed;

          // 4. 
          if (!this.isDeleting && this.charIndex === currentPhrase.length) {
            // 
            nextSpeed = this.pauseTime;
            this.isDeleting = true;
          } else if (this.isDeleting && this.charIndex === 0) {
            // 
            this.isDeleting = false;
            this.phraseIndex = (this.phraseIndex + 1) % this.typewriterPhrases.length;
            nextSpeed = 500; // 
          }

          this.typewriterTimer = setTimeout(() => {
            this.startTypewriter();
          }, nextSpeed);
        },


    initBannerMatrix() {
      const canvas = this.$refs.bannerCanvas;
      if (!canvas) return;
      
      const ctx = canvas.getContext('2d');
      const parent = canvas.parentElement;

      canvas.width = parent.offsetWidth;
      canvas.height = parent.offsetHeight;

      const chars = '01';
      const fontSize = 14;
      const columns = canvas.width / fontSize;
      const drops = [];

      for (let x = 0; x < columns; x++) {
        drops[x] = 1;
      }

      const draw = () => {
        ctx.fillStyle = 'rgba(0, 0, 0, 0.05)';
        ctx.fillRect(0, 0, canvas.width, canvas.height);
        ctx.fillStyle = '#0F0'; 
        ctx.font = fontSize + 'px monospace';

        for (let i = 0; i < drops.length; i++) {
          const text = chars.charAt(Math.floor(Math.random() * chars.length));
          ctx.fillText(text, i * fontSize, drops[i] * fontSize);
          if (drops[i] * fontSize > canvas.height && Math.random() > 0.975) {
            drops[i] = 0;
          }
          drops[i]++;
        }
      };

      this.stopBannerMatrix();
      this.bannerInterval = setInterval(draw, 53);
    },

    stopBannerMatrix() {
      if (this.bannerInterval) {
        clearInterval(this.bannerInterval);
        this.bannerInterval = null;
      }
    },

    handleResize() {
      if (this.resizeTimer) clearTimeout(this.resizeTimer);
      this.resizeTimer = setTimeout(() => {
        if (this.currentTheme === 'dark') {
          this.initBannerMatrix();
        }
      }, 200);
    },

    getCategoryCount(category) {
      if (category.subCategories && category.subCategories.length > 0) {
        return category.subCategories.reduce((acc, sub) => acc + (sub.links ? sub.links.length : 0), 0);
      }
      return category.links ? category.links.length : 0;
    },
    normalizeTags(link) {
      if (Array.isArray(link.tags)) return link.tags;
      if (typeof link.tag === 'string' && link.tag.trim()) return [link.tag.trim()];
      return [];
    },
    getTagType(tag) {
      const t = (tag || '').trim();
      if (['', 'Open Source', 'OSS'].includes(t)) return 'success';
      if (['', 'Free'].includes(t)) return 'info';
      if (['', '', 'Pro', ''].includes(t)) return 'warning';
      if (['', 'Beta'].includes(t)) return 'danger';
      return null;
    },
    getFaviconUrl(url) {
      try {
        const domain = new URL(url).hostname;
        return `https://favicon.yandex.net/favicon/${domain}`;
      } catch {
        return '/default-favicon.ico';
      }
    },
    handleImageError(event, url) {
      const img = event.target;
      try {
        const domain = new URL(url).hostname;
        if (img.src.includes('yandex.net')) {
          img.src = `https://www.google.com/s2/favicons?domain=${domain}&sz=32`;
        } else {
           img.src = '/default-favicon.ico'; 
        }
      } catch (e) {
        img.style.display = 'none';
      }
    },
    performSearch() {
      const query = this.localSearchQuery.trim();
      if (!query) return;
      const map = {
        Google: `https://www.google.com/search?q=`,
        Bing: `https://www.bing.com/search?q=`,
        Baidu: `https://www.baidu.com/s?wd=`,
        Sogou: `https://www.sogou.com/web?query=`
      };
      const prefix = map[this.searchMode] || map.Google;
      window.open(prefix + encodeURIComponent(query), '_blank');
    },
    handlePageChange(page) { this.currentPage = page; },

    scrollToCategory(categoryName) {
      if (!categoryName) return;
      const idx = this.filteredCategories.findIndex(c => c.name === categoryName);
      if (idx === -1) return;
      const targetPage = Math.floor(idx / this.pageSize) + 1;
      if (this.currentPage !== targetPage) this.currentPage = targetPage;
      this.$nextTick(() => {
        const el = document.querySelector(`[data-category="${categoryName}"]`);
        if (el) el.scrollIntoView({ behavior: 'smooth', block: 'start' });
      });
    }
  }
}
</script>

