<template>
  <el-menu
    :default-active="activeIndex"
    class="el-menu-demo"
    :class="{ 'dark-theme': currentTheme === 'dark' }"
    mode="horizontal"
    :ellipsis="false" 
  >
    <el-menu-item index="logo" class="logo-item" @click="handleHome">
      <span class="site-title hidden-xs-only">NavX</span>
    </el-menu-item>

    <el-menu-item index="toggle-menu" @click="$emit('toggle-mobile-menu')" class="mobile-menu-toggle">
      <el-icon>
        <Expand />
      </el-icon>
    </el-menu-item>

    <el-menu-item index="home" @click="handleHome" class="home-menu-item">
      <el-icon><House /></el-icon>
      <span class="hidden-xs-only"></span> 
    </el-menu-item>
    
    <el-menu-item index="donate" @click="$emit('show-donate')" title="" class="hidden-xs-only">
      <el-icon><Coffee /></el-icon>
      <span></span>
    </el-menu-item>

    <el-menu-item index="theme" @click="$emit('toggle-theme')">
      <el-icon>
        <Sunny v-if="currentTheme === 'dark'" />
        <Moon v-else />
      </el-icon>
      <span class="hidden-xs-only"></span>
    </el-menu-item>

    <el-menu-item
      index="search"
      class="search-item"
      style="margin-left: auto; border-bottom: none !important;" 
    >
      <div 
        class="search-container" 
        @click.stop 
        @mousedown.stop 
        @touchstart.stop
      >
        <canvas 
          v-if="currentTheme === 'dark'" 
          ref="searchCanvas" 
          class="search-matrix-bg"
        ></canvas>

        <el-select
          v-model="selectedEngine"
          class="search-engine-select"
          :popper-class="currentTheme === 'dark' ? 'hacker-dropdown' : ''"
          :style="{ width: isMobile ? '80px' : '160px' }"
        >
          <template #prefix>
            <el-icon class="search-engine-icon">
              <component :is="currentEngineIcon" />
            </el-icon>
          </template>

          <el-option
            v-for="engine in searchEngines"
            :key="engine.value"
            :label="engine.label"
            :value="engine.value"
          >
            <div class="search-engine-option-content">
              <el-icon class="search-engine-icon" style="margin-right: 8px; vertical-align: middle;">
                <component :is="engine.icon" />
              </el-icon>
              <span>{{ engine.label }}</span>
            </div>
          </el-option>
        </el-select>

        <el-input
          ref="searchInput"
          v-model="searchText"
          @keyup.enter="handleSearch"
          class="search-input"
          @keydown="handleKeyDown"
          placeholder=">"
          :style="{ width: isMobile ? '160px' : '260px' }"
        >
          <template #append>
            <el-button @click="handleSearch" class="search-btn-append">
              <el-icon><Search /></el-icon>
            </el-button>
          </template>
        </el-input>
        
        <el-button 
          v-if="searchText"
          @click="clearSearch" 
          circle
          size="small"
          class="clear-button"
          :class="{ 'dark-theme': currentTheme === 'dark' }"
        >
          <el-icon><Close /></el-icon>
        </el-button>

      </div>
    </el-menu-item>
  </el-menu>
</template>

<script>
import { House, Coffee, Moon, Sunny, Search, Close, ChromeFilled, VideoPlay, Expand } from '@element-plus/icons-vue'

export default {
  name: 'NavigationHeader',
  components: { House, Coffee, Moon, Sunny, Search, Close, ChromeFilled, VideoPlay, Expand },

  props: {
    activeIndex: { type: String, default: '' },
    currentTheme: { type: String, required: true }
  },

  emits: ['show-about', 'show-donate', 'show-reward', 'select', 'toggle-theme', 'toggle-mobile-menu'],

  data() {
    return {
      searchText: '',
      selectedEngine: 'google',
      searchEngines: [
        { label: 'Google', value: 'google', url: 'https://www.google.com/search?q=%s', icon: 'ChromeFilled' },
        { label: 'Bing', value: 'bing', url: 'https://www.bing.com/search?q=%s', icon: 'Search' },
        { label: '', value: 'baidu', url: 'https://www.baidu.com/s?wd=%s', icon: 'Search' },
        { label: 'B', value: 'bilibili', url: 'https://www.bilibili.com/search?keyword=%s', icon: 'VideoPlay' },
        { label: 'GitHub', value: 'github', url: 'https://github.com/search?q=%s', icon: 'Search' },
        { label: 'Youtube', value: 'youtube', url: 'https://www.youtube.com/results?search_query=%s', icon: 'VideoPlay' },
      ],
      isMobile: window.innerWidth <= 768,
      resizeTimer: null,
      matrixInterval: null
    }
  },

  computed: {
    currentEngineIcon() {
      return this.searchEngines.find(engine => engine.value === this.selectedEngine)?.icon || 'Search'
    }
  },

  watch: {
    currentTheme: {
      handler(newVal) {
        if (newVal === 'dark') {
          this.$nextTick(() => {
            this.initSearchMatrix();
          });
        } else {
          this.stopMatrix();
        }
      },
      immediate: true
    }
  },

  mounted() {
    this.checkIfMobile();
    window.addEventListener('resize', this.handleResize);
    window.addEventListener('keydown', this.handleKeyDown);
  },

  beforeUnmount() {
    window.removeEventListener('resize', this.handleResize);
    window.removeEventListener('keydown', this.handleKeyDown);
    this.stopMatrix();
  },

  methods: {
    initSearchMatrix() {
      const canvas = this.$refs.searchCanvas;
      if (!canvas) return;

      const ctx = canvas.getContext('2d');
      const parent = canvas.parentElement;
      canvas.width = parent.offsetWidth;
      canvas.height = parent.offsetHeight;

      const chars = '01';
      const fontSize = 14;
      const columns = canvas.width / fontSize;
      const drops = [];

      for (let x = 0; x < columns; x++) {
        drops[x] = 1;
      }

      const draw = () => {
        ctx.fillStyle = 'rgba(0, 0, 0, 0.1)';
        ctx.fillRect(0, 0, canvas.width, canvas.height);
        ctx.fillStyle = '#0F0';
        ctx.font = fontSize + 'px monospace';

        for (let i = 0; i < drops.length; i++) {
          const text = chars.charAt(Math.floor(Math.random() * chars.length));
          ctx.fillText(text, i * fontSize, drops[i] * fontSize);
          if (drops[i] * fontSize > canvas.height && Math.random() > 0.975) {
            drops[i] = 0;
          }
          drops[i]++;
        }
      };
      this.stopMatrix();
      this.matrixInterval = setInterval(draw, 50);
    },

    stopMatrix() {
      if (this.matrixInterval) {
        clearInterval(this.matrixInterval);
        this.matrixInterval = null;
      }
    },

    handleResize() {
      if (this.resizeTimer) clearTimeout(this.resizeTimer);
      this.resizeTimer = setTimeout(() => {
        this.checkIfMobile();
        if (this.currentTheme === 'dark') {
          this.initSearchMatrix();
        }
      }, 100);
    },

    checkIfMobile() {
      this.isMobile = window.innerWidth <= 768;
    },

    handleKeyDown(event) {
      const input = this.$refs.searchInput?.input;
      if (!input || document.activeElement !== input) return;
      if (event.isComposing) return;
    },

    handleHome() {
      window.location.href = '/';
    },

    handleSearch() {
      if (!this.searchText) return;
      const searchUrl = this.searchEngines.find(engine => engine.value === this.selectedEngine)?.url;
      if (searchUrl) {
        window.open(searchUrl.replace('%s', encodeURIComponent(this.searchText)), '_blank');
      }
    },

    clearSearch() {
      this.searchText = '';
      this.$nextTick(() => this.$refs.searchInput?.focus());
    }
  }
}
</script>

